

























































































import { Vue, Component } from 'vue-property-decorator'
import CardDetail from '@/components/CardDetail.vue'
import CalendarRow from '@/components/CalendarRow.vue'

interface Card {
    id?: string
    name: string
    holder: 'conan' | 'chaeji'
    period_start: string
    period_end: string
    status: string
}

@Component({
    components: {
        CardDetail,
        CalendarRow
    }
})
export default class Information extends Vue {
    monthNames: string[] = [
        '1Ïõî', '2Ïõî', '3Ïõî', '4Ïõî', '5Ïõî', '6Ïõî',
        '7Ïõî', '8Ïõî', '9Ïõî', '10Ïõî', '11Ïõî', '12Ïõî'
    ]
    
    currentYear: number = new Date().getFullYear()
    minYear: number = 2020
    maxYear: number = new Date().getFullYear() + 5
    isMobile: boolean = false

    get cards(): Card[] {
        return this.$store.getters.cards || []
    }

    get selectedCard(): Card | null {
        return this.$store.getters.selectedCard || null
    }

    get visibleCards(): Card[] {
        const filteredCards = this.cards.filter(card => {
            const startYear = new Date(card.period_start).getFullYear()
            const endYear = new Date(card.period_end).getFullYear()
            
            return this.currentYear >= startYear && this.currentYear <= endYear
        })

        // ÏÉÅÌÉúÎ≥Ñ, ÏÇ¨Ïö©ÏûêÎ≥ÑÎ°ú Ï†ïÎ†¨ (Ìï¥ÏßÄÎêú Ïπ¥ÎìúÎäî ÌïòÎã®Ïóê)
        return filteredCards.sort((a, b) => {
            // Î®ºÏ†Ä ÏÉÅÌÉúÎ≥ÑÎ°ú Ï†ïÎ†¨ (Ìï¥ÏßÄÎêòÏßÄ ÏïäÏùÄ Ïπ¥ÎìúÍ∞Ä Î®ºÏ†Ä)
            if (a.status === 'terminated' && b.status !== 'terminated') return 1
            if (a.status !== 'terminated' && b.status === 'terminated') return -1
            
            // Í∞ôÏùÄ ÏÉÅÌÉú ÎÇ¥ÏóêÏÑúÎäî ÏÇ¨Ïö©ÏûêÎ≥ÑÎ°ú Ï†ïÎ†¨
            if (a.holder === b.holder) {
                // Í∞ôÏùÄ ÏÇ¨Ïö©Ïûê ÎÇ¥ÏóêÏÑúÎäî ÏãúÏûëÏùºÎ°ú Ï†ïÎ†¨
                return new Date(a.period_start).getTime() - new Date(b.period_start).getTime()
            }
            return a.holder === 'conan' ? -1 : 1
        })
    }

    get timelineHeight(): string {
        const rowHeight = 24
        const rowSpacing = 2
        const totalHeight = this.visibleCards.length * (rowHeight + rowSpacing) + 100 // Ïó¨Ïú† Í≥µÍ∞Ñ
        return `${Math.max(totalHeight, 400)}px`
    }

    previousYear(): void {
        if (this.currentYear > this.minYear) {
            this.currentYear--
        }
    }

    nextYear(): void {
        if (this.currentYear < this.maxYear) {
            this.currentYear++
        }
    }

    goToCurrentYear(): void {
        this.currentYear = new Date().getFullYear()
    }

    closeCardDetail(): void {
        this.$store.commit('reset')
    }

    getStatusText(status: string): string {
        const statusMap: Record<string, string> = {
            'using': 'ÏÇ¨Ïö©Ï§ë',
            'keeping': 'Î≥¥Í¥ÄÏ§ë',
            'terminated': 'Ìï¥ÏßÄÎê®'
        }
        
        return statusMap[status] || status
    }

    shouldShowUserDivider(card: Card, index: number): boolean {
        // Ï≤´ Î≤àÏß∏ Ïπ¥ÎìúÏù¥Í±∞ÎÇò Ïù¥Ï†Ñ Ïπ¥ÎìúÏôÄ Îã§Î•∏ ÏÇ¨Ïö©ÏûêÏù∏ Í≤ΩÏö∞ Íµ¨Î∂ÑÏÑ† ÌëúÏãú
        if (index === 0) return true
        
        const previousCard = this.visibleCards[index - 1]
        return previousCard.holder !== card.holder
    }

    getHolderName(holder: string): string {
        const holderNames: Record<string, string> = {
            'conan': 'Conan',
            'chaeji': 'Chaeji'
        }
        return holderNames[holder] || holder
    }

    getAdjustedRowIndex(index: number): number {
        // Íµ¨Î∂ÑÏÑ†ÏùÑ Í≥†Î†§ÌïòÏó¨ Ïã§Ï†ú Ìñâ Ïù∏Îç±Ïä§ Í≥ÑÏÇ∞
        let adjustedIndex = index
        
        // ÌòÑÏû¨ Ïù∏Îç±Ïä§Î≥¥Îã§ ÏïûÏóê ÏûàÎäî Íµ¨Î∂ÑÏÑ†Ïùò Í∞úÏàòÎ•º Í≥ÑÏÇ∞
        for (let i = 0; i < index; i++) {
            if (this.shouldShowUserDivider(this.visibleCards[i], i)) {
                adjustedIndex++
            }
        }
        
        return adjustedIndex
    }

    getDividerTopPosition(index: number): number {
        const rowHeight = 60
        const rowSpacing = 8
        const dividerHeight = 30
        
        let topPosition = 0
        
        // ÌòÑÏû¨ Ïù∏Îç±Ïä§ÍπåÏßÄÏùò Î™®Îì† Ïπ¥ÎìúÏôÄ Íµ¨Î∂ÑÏÑ†Ïùò ÎÜíÏù¥Î•º Í≥ÑÏÇ∞
        for (let i = 0; i < index; i++) {
            if (this.shouldShowUserDivider(this.visibleCards[i], i)) {
                topPosition += dividerHeight
            }
            topPosition += rowHeight + rowSpacing
        }
        
        return topPosition
    }

    checkMobile(): void {
        this.isMobile = window.innerWidth <= 768
    }

    mounted(): void {
        this.checkMobile()
        window.addEventListener('resize', this.checkMobile)
    }

    beforeDestroy(): void {
        window.removeEventListener('resize', this.checkMobile)
    }
}
